<template>
    <div class="content">
        <div class="intro-y col-span-12 lg:col-span-12">
            <div class="intro-y box">
                <div
                    class="flex flex-col sm:flex-row items-center p-5 border-b border-slate-200/60 dark:border-darkmode-400">
                    <h2 class="font-medium text-base mr-auto">
                        Add Service Form
                    </h2>
                </div>
                <div id="vertical-form" class="p-5">
                    <div class="preview">
                        <form @submit.prevent="handleSubmit" method="post">
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Title</label>
                                <input id="vertical-form-2" type="text" class="form-control" placeholder="Title"
                                    v-model="form.title" name="title">
                                <p v-if="formErrors.title" class="text-danger">{{ formErrors.title }}</p>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Sub Title</label>
                                <input id="vertical-form-2" type="text" class="form-control" placeholder="Subtitle"
                                    v-model="form.subtitle" name="subtitle">
                                <p v-if="formErrors.subtitle" class="text-danger">{{ formErrors.subtitle }}</p>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Rating</label>
                                <input id="vertical-form-2" type="text" class="form-control" placeholder="Rating"
                                    v-model="form.rating">
                                <p v-if="formErrors.rating" class="text-danger" min="0" max="5">{{ formErrors.rating }}</p>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Reviews</label>
                                <input id="vertical-form-2" type="text" class="form-control" placeholder="Reviews"
                                    v-model="form.reviews" name="reviews">
                                <p v-if="formErrors.reviews" class="text-danger">{{ formErrors.reviews }}</p>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Price</label>
                                <input id="vertical-form-2" type="text" class="form-control" v-model="form.price"
                                    name="price" />
                                <p v-if="formErrors.price" class="text-danger">{{ formErrors.price }}</p>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Image</label>
                                <input id="vertical-form-2" type="file" class="form-control" @change="handleFileChange"
                                    name="image" />
                                <p v-if="formErrors.image" class="text-danger">{{ formErrors.image }}</p>
                            </div>
                            <button class="btn btn-primary mt-5" type="submit">Add</button>

                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { postRequest } from '../../pages/ApiFunctions'
import * as yup from 'yup'
const validationSchema = yup.object().shape({
    title: yup.string().required('Title is required'),
    subtitle: yup.string().required('Subtitle is required'),
    rating: yup.number().required('Rating is required').min(1).max(5),
    reviews: yup.string().required('Reviews is required'),
    price: yup.number().required('Price is required').min(0),
    image: yup.mixed()
        .test('fileType', 'Image must be a JPG or PNG', (value) => {
            if (!value) {
                return true; // Empty file is allowed
            }
            return ['image/jpeg', 'image/png', 'image/jpg'].includes(value.type);
        })
        .required('Image is required'),
})
export default {
    name: 'AddService',
    data() {
        return {
            form: {
                title: '',
                subtitle: '',
                rating: null,
                reviews: null,
                price: null,
                image: null,
            },
            formErrors: {},
        }
    },

    methods: {
        handleFileChange(event) {
            const file = event.target.files[0]
            this.form.image = file
        },
        async handleSubmit() {
            try {
                await validationSchema.validate(this.form, { abortEarly: false })
                const formData = new FormData()
                formData.append('image', this.form.image)
                formData.append('title', this.form.title)
                formData.append('subtitle', this.form.subtitle)
                formData.append('rating', this.form.rating)
                formData.append('reviews', this.form.reviews)
                formData.append('price', this.form.price)
                await postRequest('/insert-service', formData)
                this.$router.push({ name: 'ServicesTable' })
            } 
            catch (error) {
                if (error.name === 'ValidationError') {
                    const formErrors = {}
                    error.inner.forEach((e) => {
                        formErrors[e.path] = e.message
                    })
                    this.formErrors = formErrors
                }
            }
        }
    },

    computed: {
        formIsEmpty() {
            return !this.title || !this.subtitle || !this.rating || !this.reviews || !this.price || !this.image
        }
    }
}
</script>



